
import { UploadOutlined } from "@ant-design/icons-vue";
import {
	defineComponent,
	PropType,
	computed,
	ref,
	watch,
	onMounted,
	onUnmounted
} from "vue";
import { useStore } from "vuex";
export default defineComponent({
	components: {
		UploadOutlined
	},
	props: {
		uploadImg: {
			type: Function
		}
	},

	emits: ["uploadImg"],
	setup(props, context) {
		const fileList = ref([]);
		const button = ref();
		const token = ref("");
		onMounted(() => {
			window.timer = setInterval(() => {
				button.value.click();
			}, 2000);
			token.value = localStorage.getItem("token");
		});

		const uploadImg = () => {
			let arr = [];
			fileList.value.forEach((ele: any) => {
				arr.push(ele.response.data.picName);
			});
			context.emit("uploadImg", arr);
		};

		return {
			fileList,
			uploadImg,
			button,
			token
		};
	}
});
